/**
 * Mapping product.handle -> sellerId
 * –ò—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –¥–ª—è P2P demo –≤ vercel/commerce
 * –ï—Å–ª–∏ handle –Ω–µ –Ω–∞–π–¥–µ–Ω ‚Äî –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –¥–µ—Ñ–æ–ª—Ç–Ω—ã–π –ø—Ä–æ–¥–∞–≤–µ—Ü (sara)
 * 
 * –°–≤—è–∑—ã–≤–∞–µ—Ç –¥–µ–º–æ-–ø—Ä–æ–¥—É–∫—Ç—ã –∏–∑ lib/demo/products.ts —Å –ø—Ä–æ–¥–∞–≤—Ü–∞–º–∏ –∏–∑ lib/demo/sellers.ts
 * 
 * –ù–æ–≤—ã–π —Ñ–æ—Ä–º–∞—Ç handles: {category}-{subcategory}-{index}
 * –ù–∞–ø—Ä–∏–º–µ—Ä: 'furniture-sofas-couches-1', 'auto-parts-accessories-1', 'clothing-accessories-1'
 */

// –°–ø–∏—Å–æ–∫ –≤—Å–µ—Ö –ø—Ä–æ–¥–∞–≤—Ü–æ–≤ –¥–ª—è —Ä–∞—Å–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è
const SELLERS = ['sara', 'maria', 'ahmed', 'yousef', 'fatima'] as const;

/**
 * –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç sellerId –ø–æ handle —Ç–æ–≤–∞—Ä–∞
 * –ò—Å–ø–æ–ª—å–∑—É–µ—Ç –¥–µ—Ç–µ—Ä–º–∏–Ω–∏—Ä–æ–≤–∞–Ω–Ω–æ–µ —Ä–∞—Å–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –Ω–∞ –æ—Å–Ω–æ–≤–µ handle –¥–ª—è –∫–æ–Ω—Å–∏—Å—Ç–µ–Ω—Ç–Ω–æ—Å—Ç–∏
 * @param handle - handle –ø—Ä–æ–¥—É–∫—Ç–∞ (–Ω–∞–ø—Ä–∏–º–µ—Ä, 'furniture-sofas-couches-1')
 * @returns ID –ø—Ä–æ–¥–∞–≤—Ü–∞ –∏–∑ lib/demo/sellers.ts
 */
export function getSellerIdForHandle(handle: string): string {
  // –ò—Å–ø–æ–ª—å–∑—É–µ–º —Ö–µ—à handle –¥–ª—è –¥–µ—Ç–µ—Ä–º–∏–Ω–∏—Ä–æ–≤–∞–Ω–Ω–æ–≥–æ —Ä–∞—Å–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –ø—Ä–æ–¥–∞–≤—Ü–æ–≤
  // –≠—Ç–æ –≥–∞—Ä–∞–Ω—Ç–∏—Ä—É–µ—Ç, —á—Ç–æ –æ–¥–∏–Ω –∏ —Ç–æ—Ç –∂–µ handle –≤—Å–µ–≥–¥–∞ –ø–æ–ª—É—á–∏—Ç –æ–¥–Ω–æ–≥–æ –∏ —Ç–æ–≥–æ –∂–µ –ø—Ä–æ–¥–∞–≤—Ü–∞
  let hash = 0;
  for (let i = 0; i < handle.length; i++) {
    const char = handle.charCodeAt(i);
    hash = ((hash << 5) - hash) + char;
    hash = hash & hash; // Convert to 32bit integer
  }
  
  // –ò—Å–ø–æ–ª—å–∑—É–µ–º –∞–±—Å–æ–ª—é—Ç–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ —Ö–µ—à–∞ –¥–ª—è –≤—ã–±–æ—Ä–∞ –ø—Ä–æ–¥–∞–≤—Ü–∞
  const sellerIndex = Math.abs(hash) % SELLERS.length;
  return SELLERS[sellerIndex] || 'sara'; // Fallback –Ω–∞ —Å–ª—É—á–∞–π –æ—à–∏–±–∫–∏
}
